.c-option {
    display: flex;
    flex-direction: column;

    margin: 3px 0;

    // position: relative;

    &__label {
        display: inline-block;
        // width: ;
        // height: ;
        // margin: 20px 1% 20px 0;
        vertical-align: top;
        text-align: center; 
        
        label {
            border: 1px solid rgba(#ffb2b2,.9);
            // border: 1px solid rgba(#ffb2b2,.15);
        	// display: block;

            display: flex;
            flex-direction: column;

            align-items: center;
            justify-content: center;

        	height: 75px;
        	width: 75px;
        	// padding: 10px 10px;
        	cursor: pointer;
        	opacity: .6;
        	transition: all .5s ease-in-out;
        	&:hover, &:focus, &:active {
                opacity: 1;
        		border: 1px solid rgba(#ffb2b2,.5);
        	}
            
            span {
                &:nth-child(1) {
                    font-size: 22px;
                }
                &:nth-child(2) {
                    font-size: 12px;
                }
            }
        }

        input[type="radio"] {
            position: absolute;
            opacity: 0;
            width: 0;
            height: 0;
            
            &:checked ~ label {
              opacity: 1;
              border: 1px solid #ffb2b2;
            }
        }
    }
}
